.hero{
  width: 1680px;
}

  .hero__container{
    margin-left: 183px;
    margin-right: 75px ;
    width: 1422px;
}

.hero__title{
  @include font($fs: 58px, $fw: 800);
  letter-spacing: 1.74px;
  line-height: normal;
  color: var(--text-color);
  margin-bottom: 15px;
}

.hero__paragraph{
  @include font($fs: 20px, $fw: 500);
  letter-spacing: 1.6px;
  line-height: normal;
  color: var(--text-color);
  margin-bottom: 45px;

  &--color {
    color: var(--link-color);
}}

.hero-wave{
  width: 1680px;
  height: 925px;
  outline: 2px solid black;
  filter: drop-shadow(-5px 21px 26px rgba(0, 0, 0, 0.04));
}
