
.logo{

    gap: 4px;
    align-items: baseline;
    justify-content:center ;
    margin-right: 143.59px;

}

.logo-icon-campfire{
    display: inline-flex;
    align-items: baseline;
    justify-content: center;

    width: 22px;
    height: 30px;
}
.logo__title{
    @include font($fs: 26px, $fw: 800);
    letter-spacing: 1.04px;
    line-height: normal;
}

.logo__text{
    display: block;

    @include font($fs: 8px, $fw: 700);
    letter-spacing: 1.44px;
    line-height: normal;
    
    font-family: var(---text-color);
    padding-left: 2px;

    &--color {
    color: var(--accent-color);
    }
}