.navigation__btn {
    @include font($fs: 14px, $fw: 700);
    letter-spacing: 1.96px;
    line-height: normal;
    text-transform: uppercase;
    color: var(--link-color);
    border: 2px solid var(--link-color);

    text-align: center;
    padding-top: 27px;
    padding-right: 22.96px;
    padding-bottom: 26px;
    padding-left: 23.96px;

    transition: {
        property: color, background-color, border-color, filter;
        duration: 250ms;
        timing-function: var(--function-transition);
    }
    &--active,
    &:focus,
    &:hover {
    color: var(--title-color-white);
    background-color: var(--accent-color);
    border-color: var(--accent-color);
    filter: var(--drop-shadow-btn);}
}

