
.header{
    width: 1680px;
}

.header__container{
    padding-top: 42px;
    padding-right: 0;
    margin-bottom: 30px;
    width: 1314px;

}
.navigation{
    display: flex;
    align-items: center;
}

.navigation__link{
    transition: {
        property: color, background-color, box-shadow;
        duration: 250ms;
        timing-function: var(--function-transition);}

    &--active,
    &:focus,
    &:hover{
        color: var(--accent-color);
    }
}

.navigation__list{
    display: flex;
    margin: 0;
    padding: 0;
}

.navigation__item{
    display: flex;

    @include font($fs: 14px, $fw: 700);
    letter-spacing: 1.04px;
    line-height: normal;
    text-transform: uppercase;
    color: var(--link-color);
}

.navigation__item:not(:last-child){
    margin-right: 39.94px;
}
.navigation__item:last-child{
    margin-right: 59.92px;
}